import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { axiosPrivate } from './Api/axios';
import { PieChart, Pie, Sector, Cell, Legend, Tooltip, ResponsiveContainer } from 'recharts';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faShapes } from '@fortawesome/free-solid-svg-icons';
import DatePicker, { DateObject } from 'react-multi-date-picker';
import persian from "react-date-object/calendars/persian";
import persian_fa from "react-date-object/locales/persian_fa";
import useAuth from './Hooks/useAuth';


const MyPieChart = () => {

    const navigate = useNavigate();
    const [catCl, setCatCl] = useState('telegram_channel');
    const [pieData, setPieData] = useState([]);
    const { setTaging } = useAuth();
    const [value, setValue] = useState()
    const date = new DateObject({ calendar: persian, locale: persian_fa })
    const [day, setDay] = useState(date.day>9 ?date.day :`0${date.day}`)
    const [month, setMonth] = useState(date.month.number>9 ?date.month.number :`0${date.month.number}`)
    const [year, setYear] = useState(date.year)
    const [fullDate, setFullDate] = useState(`${year}-${month}-${day}`)

    const COLORS = ['#327F51', '#EEB64B', '#9FB4C7', '#A92F5F', '#E54264', '#283D5E', '#C3C48A'];

    const RADIAN = Math.PI / 180;
    const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent, index }) => {
        const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
        const x = cx + radius * Math.cos(-midAngle * RADIAN);
        const y = cy + radius * Math.sin(-midAngle * RADIAN);

        return (
            <text x={x} y={y} fill="white" textAnchor={x > cx ? 'start' : 'end'} dominantBaseline="central">
                {`${(percent * 100).toFixed(0)}%`}
            </text>
        );
    };

    function turnToTelegramChannel() {
        setCatCl('telegram_channel');
    }

    function turnToTelegramGroups() {
        setCatCl('telegram_groups');
    }

    function turnToInsta() {
        setCatCl('instagram');
    }

    function turnToTweeter() {
        setCatCl('twitter');
    }

    function turnToPost() {
        setCatCl('news_agency');
    }

    const actFilter = {
        backgroundColor: "rgb(241 245 249 / 0.2)"
    };
    const dActFilter = {
        backgroundColor: "transparent"
    }

    const[count, setCount] = useState(0)

    function setMyDate() {
        setCount(count+1);
        setDay(value.day>9 ?value.day :`0${value.day}`);
        setMonth(value.month.number>9 ?value.month.number :`0${value.month.number}`);
        setYear(value.year);
        setFullDate(`${year}-${month}-${day}`);
        console.log(value);
    }

    useEffect(() => {
        let isMounted = true;
        const controller = new AbortController();

        const getPie = async () => {
            try {
                const response = await axiosPrivate.get(`/${catCl}/sunburst_chart_data?charts_time_filter=${fullDate}`,
                    {
                        signal: controller.signal
                    });
                isMounted && setPieData(response?.data);
                console.log(pieData);
            } catch (err) {
                console.error(err);
            }
        }

        console.log(fullDate);
        console.log(value);
        console.log(date.day);
        console.log(count);

        getPie();

        return () => {
            isMounted = false;
            isMounted && controller.abort();
        }
    }, [count, catCl])

    function navi() {
        navigate('/searchnews');
    }

    console.log(pieData);

    // const handleDownloadReports = () =>{
    //        const url = window.URL.createObjectURL(new Blob([pieData])) 
    //        const link = document.createElement('a')
    //        link.href = url
    //        const fileName = `abbas.csv`;
    //        link.setAttribute('download', fileName)
    //        document.body.appendChild(link)
    //        link.click()
    //        link.remove()
    //    }

    return (
        <>
            <h5 className='mt-8 px-4'><span className='inline-block place-content-center align-top text-middle-blue text-xl ml-2'><FontAwesomeIcon icon={faShapes} /></span>عملیات روانی</h5>
            <div className='psyc-container min-h-fit bg-white border-slate-400 rounded-md p-4 mt-4'>
                <div className='grid'>
                    <nav className='bg-middle-blue rounded-lg mb-4'>
                        <ul className='flex m-0'>
                            <li className='text-white text-center p-3 rounded-lg bg-dark-blue'>دسته مورد نظر را انتخاب کنید:</li>
                            <li className='text-white p-3 hover:bg-slate-100/20' style={catCl === "telegram_channel" ? actFilter : dActFilter} onClick={turnToTelegramChannel}>کانال‌های تلگرام</li>
                            <li className='text-white p-3 hover:bg-slate-100/20' style={catCl === "telegram_groups" ? actFilter : dActFilter} onClick={turnToTelegramGroups}>گروه‌های تلگرام</li>
                            <li className='text-white p-3 hover:bg-slate-100/20' style={catCl === "twitter" ? actFilter : dActFilter} onClick={turnToTweeter}>توییتر</li>
                            <li className='text-white p-3 hover:bg-slate-100/20' style={catCl === "instagram" ? actFilter : dActFilter} onClick={turnToInsta}>اینستاگرام</li>
                            <li className='text-white p-3 hover:bg-slate-100/20' style={catCl === "news_agency" ? actFilter : dActFilter} onClick={turnToPost}>خبرگذاری‌ها</li>
                        </ul>
                    </nav>
                    {/* <button onClick={handleDownloadReports}>دانلود گزارش</button> */}
                    <div className='flex justify-center'>
                        <DatePicker
                            placeholder='لطفا تاریخ را انتخاب بفرمایید'
                            value={value}
                            onChange={setValue}
                            calendar={persian}
                            locale={persian_fa}
                            calendarPosition="bottom-right"
                        />
                        {/* {value?.toDate?.().toString()} */}
                        <button className=' bg-dark-blue text-white hover:bg-middle-blue px-4 mr-2 rounded-md' onClick={setMyDate}>فیلتر</button>
                    </div>
                    <ResponsiveContainer width="100%" aspect={3}>
                        <PieChart width={500} height={500}>
                            <Pie
                                data={pieData}
                                cx="50%"
                                cy="50%"
                                innerRadius={65}
                                outerRadius={125}
                                fill="#8884d8"
                                paddingAngle={6}
                                dataKey="value"
                                legendType="circle"
                            >
                                {pieData?.map((entry, index) => (
                                    <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} onClick={() => { setTaging(entry.name); navi() }} />
                                ))}
                            </Pie>
                            <Tooltip coordinate={{ x: 0, y: 0 }} />
                            <Legend align='center' width={600} wrapperStyle={{ transform: "translate(-50%, -50%)", left: "50%", bottom: "-10%" }}></Legend>
                        </PieChart>
                    </ResponsiveContainer>
                </div>
            </div></>
    )
}

export default MyPieChart